/* Services */
.home-services{
    width: 100%;
    background: var(--bg);
    display: flex;
    flex-direction: column;
    gap: 2em;
    padding: 4em 0;
}

.home-services-top{
    padding: 0 8em;
    display: flex;
    flex-direction: column;
    gap: 1em;
}
.home-services-top > p{
    color: var(--font-light);
    line-height: 1.5em;
    font-size: 1.2rem;
}
.home-services-top > p span{
    font-weight: 600;
    color: var(--font-clr);
}


.home-services-links{
    position: relative;
    display: flex;
    flex-direction: column;
}
.home-service-link{
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    gap: 1em;
    padding: 2em 8em;
    background: transparent;
    transition: all 0.3s;
    cursor: pointer;
    border-bottom: 1px solid black;
}
.home-service-link:nth-child(4){
    border-bottom: none;
}
.home-service-link-overlay{
    position: absolute;
    z-index: -1;
    background: var(--blue-dark);
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transform: scaleX(0);
    transform-origin: left;
    opacity: 0;
}
/* .home-service-link:hover{
    padding:2em 4em;
} */
.home-service-link:hover .home-service-link-p{
    color: white;
}
.home-service-link:hover .home-service-link-small{
    color: white;
}
.home-service-link:hover .home-service-link-btn{
    background: white;
    color: var(--font-clr);
}

.home-service-link-content{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.home-service-link-content > div{
    width: 80%;
    display: flex;
    align-items: center;
}
.home-service-link-p{
    color: var(--font-clr);
    font-size: 3rem;
    font-weight: 500;
    user-select: none;
    width: 40%;
}
.home-service-link-small{
    width: 60%;
    font-size: 1rem;
    line-height: 1.5em;
}
.home-service-link-btn{
    width: 50px;
    height: 50px;
    border: 1px solid white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: var(--blue-dark);
    color: white;
    font-size: 1.2rem;
    transition: all 0.3s;
}
.home-service-link-btn:hover{
    transform: translateX(10px);
}


/* MEDIA QUERIES */
@media only screen and (max-width: 980px){
    .home-services{
        padding: 4em 0em;
    }
    .home-services-top{
        padding: 0 2em;
    }
    .home-services > p{
        width: 100%;
    }

    .home-services-links{
        margin-top: 2em;
    }
    .home-service-link{
        padding: 2em;
    }.home-service-link-content > div{
        flex-direction: column;
        gap: 1em;
    }
    .home-service-link-p{
        font-size: 2rem;
        width: 100%;
    }
    .home-service-link-small{
        width: 100%;
        font-size: 1rem;
    }
    .home-service-link:hover{
        padding: 2em;
    }
    .home-service-link-btn{
        font-size: 1rem;
        width: 40px;
        height: 40px;
    }
}

@media only screen and (max-width: 480px){
    .home-services{
        padding: 4em 0em;
    }
    .home-services-top{
        padding: 0 1em;
    }
    .home-services > p{
        width: 100%;
    }

    .home-services-links{
        margin-top: 2em;
    }
    .home-service-link{
        padding: 1em;
    }.home-service-link-content > div{
        flex-direction: column;
        gap: 1em;
    }
    .home-service-link-p{
        font-size: 1.5rem;
        width: 100%;
    }
    .home-service-link-small{
        width: 100%;
        font-size: 0.8rem;
    }
    .home-service-link:hover{
        padding: 1em;
    }
    .home-service-link-btn{
        font-size: 1rem;
        width: 40px;
        height: 40px;
    }
}

